section.projectPage {
    display: flex;
    justify-content: space-evenly;
}

section.projectContainer {
    width: 50%;
    height: 88%;
    overflow-y: scroll;
    border-radius: 3%;
    /* background-color: grey; */
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
    /* make scrollbar transparent */
}

div.projectDescription {
    width: 40%;
}

.projectText {
    /* background-color: peru; */
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 1rem;
    height: 88%;
    margin: 0;
    
}

.projectText h1 {
    font-size: 4rem;
    overflow-wrap: break-word;
    text-align: center;

}
